import React from "react";
import Row from "muicss/lib/react/row";
import Col from "muicss/lib/react/col";

export default class DocumentLink extends React.Component {
  constructor(props) {
    super(props);
    this.handleClick = this.handleClick.bind(this);
  }

  handleClick() {
    this.props.getGoogleDoc(this.props.id);
  }

  render() {
    return (
      <div>
        <Row className="documents">
          <Col md="7" className="document-name">
            <i className="material-icons">description</i>
            <a
              href={this.props.gDriveUrl}
              target="_blank"
              rel="noopener noreferrer"
            >
              {this.props.title}
            </a>
          </Col>
          <Col md="4" className="document-share">
            <p>{this.props.shared}</p>
          </Col>
          <Col md="1" className="document-download">
            <p href="#" onClick={this.handleClick}>
              <i className="material-icons">file_download</i>
            </p>
          </Col>
        </Row>
      </div>
    );
  }
}
