import React from "react";

export default class Tab extends React.Component {
  handleClick() {
    this.props.clickFunction(this.props.id);
  }
  render() {
    return (
      <li
        className={this.props.active ? "tab tab-active" : "tab"}
        onClick={this.handleClick.bind(this)}
      >
        {this.props.title}
      </li>
    );
  }
}
