import React from "react";
import Row from "muicss/lib/react/row";
import Col from "muicss/lib/react/col";
import Divider from "muicss/lib/react/divider";
import Tab from "./Tab";
import sanitizeHtml from "sanitize-html";
import DocumentLink from "./DocumentLink";
import BrokerDocumentLink from "./DocumentLinkBroker";
import { formatTotalComps } from "../../lib";
import Modal from "react-modal";
import Form from "muicss/lib/react/form";
import Textarea from "muicss/lib/react/textarea";
import Button from "muicss/lib/react/button";
import { Navigation } from "../../lib";

export default class Deal extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      tabSelected: 1,
      modalOpen: false,
      note: "",
    };
  }

  handleTabClick(id) {
    this.setState({ tabSelected: id });
  }

  getSubmission() {
    const dirtySubmission = this.props.submission;
    const cleanSubmission = sanitizeHtml(dirtySubmission);
    if (this.props.submission) {
      return { __html: cleanSubmission };
    } else return { __html: "" };
  }

  getDocuments() {
    let documents = [];
    if (this.props.documents.length) {
      this.props.documents.map((documentTypeName) => {
        documentTypeName.info.map((doc) => {
          if (this.props.authentication.is_broker) {
            var dec = decodeURIComponent(doc.gDriveUrl);
            documents.push(
              <BrokerDocumentLink
                key={doc.fileId}
                id={doc.id}
                gDriveUrl={dec}
                shared={doc.isClientView}
                title={doc.title}
                getGoogleDoc={this.props.getGoogleDoc}
              />
            );
          } else {
            documents.push(
              <DocumentLink
                key={doc.fileId}
                id={doc.id}
                title={doc.title}
                getGoogleDoc={this.props.getGoogleDoc}
              />
            );
          }
          return documents;
        });
        return documents;
      });
    }
    return <Row>{documents}</Row>;
  }

  quoteDialog() {
    this.props.setPageAndScrollPos("/missingQuotes/quote", window.scrollY);
    let path = encodeURIComponent(window.location.pathname);
    Navigation.go(`/quote-form/1/${this.props.quote_id}/${path}`);
  }

  passDialog() {
    this.setState({ modalOpen: true });
  }

  closeDialog(e) {
    e.preventDefault();
    this.setState({ modalOpen: false, note: "" });
  }

  submitDialog(e) {
    e.preventDefault();

    if (!this.state.note) {
      this.setState({ noteRequired: true });
      return;
    }
    this.props.passDeal(this.props.quote_id, this.state.note);
    this.setState({ modalOpen: false, note: "" });
  }

  setNote(e) {
    this.setState({ note: e.target.value, noteRequired: !e.target.value });
  }

  render() {
    return (
      <div className="missing-quotes">
        <div className="panel mui-panel mq-panel">
          <Row
            className="panel-heading"
            onClick={this.props.navChange.bind(
              null,
              this.props.violations,
              this.props.name
            )}
          >
            <Col
              md="10"
              onClick={this.props.clickFunction.bind(
                this,
                this.props.dealId,
                this.props.violations,
                this.props.otherProperty,
                "violations"
              )}
            >
              <p className="panel-info-value property-title">
                <b>{this.props.name}</b>
                {this.props.firstLook ? (
                  <span className="tag primary-tag first-look-tag inline-block">
                    First Look
                  </span>
                ) : (
                  ""
                )}
              </p>
            </Col>
          </Row>
          <Row
            onClick={this.props.navChange.bind(
              null,
              this.props.violations,
              this.props.name
            )}
          >
            <div
              onClick={this.props.clickFunction.bind(
                this,
                this.props.dealId,
                this.props.violations,
                this.props.otherProperty,
                "violations"
              )}
            >
              <Col md="2">
                <p className="small-title deal-info-title">Address</p>
                <p className="panel-info-value">{this.props.address}</p>
                <p className="panel-info-value">{this.props.zip}</p>
                <p className="panel-info-value">
                  <i
                    className="orange-text orange-text-hover pointer"
                    onClick={this.props.clickFunction.bind(
                      this,
                      this.props.dealId,
                      this.props.violations,
                      this.props.otherProperty,
                      "property-details"
                    )}
                  >
                    {this.props.otherProperty === 0
                      ? null
                      : this.props.otherProperty === 1
                      ? "1 other property"
                      : this.props.otherProperty + " other properties"}
                  </i>
                </p>
              </Col>
              <div>{this.props.info}</div>
            </div>
          </Row>
          <Row>
            <div>
              <p className="quotes-btn" onClick={this.quoteDialog.bind(this)}>
                <span>{this.props.quoteText}</span>
              </p>
              {this.props.quoteText === "Edit Quote" ? (
                ""
              ) : (
                <p className="pass-btn" onClick={this.passDialog.bind(this)}>
                  <span>Pass</span>
                </p>
              )}
            </div>
          </Row>
          <Row>
            <Col md="12">
              <ul className="tabs">
                <Tab
                  id={1}
                  active={this.state.tabSelected === 1}
                  title="WRITE UP"
                  clickFunction={this.handleTabClick.bind(this)}
                />
                <Tab
                  id={2}
                  active={this.state.tabSelected === 2}
                  title="DOCUMENTS"
                  clickFunction={this.handleTabClick.bind(this)}
                />
              </ul>
            </Col>
          </Row>
          <Row>
            <Divider />
          </Row>
          <Row>
            <Col md="12">
              <div
                className={
                  this.state.tabSelected === 1 ? "tab-content" : "hidden"
                }
                dangerouslySetInnerHTML={this.getSubmission()}
              />
              <div
                className={
                  this.state.tabSelected === 2 ? "tab-content" : "hidden"
                }
              >
                {this.state.tabSelected === 2 ? this.getDocuments() : ""}
              </div>
            </Col>
          </Row>
          <Divider style={{ marginTop: "20px" }} />
          <Row>
            <Col md="12">
              <Row className="comps-row">
                <div
                  onClick={this.props.compsClick.bind(
                    null,
                    this.props.inNYC,
                    this.props.address,
                    this.props.zip,
                    this.props.property_type_id
                  )}
                >
                  <div
                    className={
                      this.props.violations !== null ? "border-right" : ""
                    }
                  >
                    <p className="violation-value comps">
                      {formatTotalComps(this.props.comps)}
                      <span>Comps</span>
                    </p>
                  </div>
                </div>
                <div
                  className={
                    this.props.violations !== null
                      ? "comps-row-violations"
                      : "hidden"
                  }
                  onClick={this.props.navChange.bind(
                    null,
                    this.props.violations,
                    this.props.name
                  )}
                >
                  <div
                    onClick={this.props.clickFunction.bind(
                      this,
                      this.props.dealId,
                      this.props.violations,
                      this.props.otherProperty,
                      "violations"
                    )}
                  >
                    <p className="violation-value">
                      {this.props.violations}
                      <span>Violations</span>
                    </p>
                  </div>
                  <div
                    onClick={this.props.clickFunction.bind(
                      this,
                      this.props.dealId,
                      this.props.violations,
                      this.props.otherProperty,
                      "violations"
                    )}
                  >
                    <p className="violation-value">
                      {this.props.complaints}
                      <span>Complaints</span>
                    </p>
                  </div>
                </div>
              </Row>
            </Col>
          </Row>
        </div>
        <Modal
          className="note-modal"
          isOpen={this.state.modalOpen}
          ariaHideApp={false}
          contentLabel="Modal"
          onAfterOpen={function () {}}
          onRequestClose={this.closeDialog.bind(this)}
          shouldCloseOnOverlayClick={true}
        >
          <Form className="modal-form">
            <p className="add-note-modal">Reason for passing </p>
            <Textarea
              rows={1}
              value={this.state.note ? this.state.note : ""}
              require
              onChange={this.setNote.bind(this)}
              className="add-note-textarea"
            />
            {this.state.noteRequired ? (
              <span className="error-block">Required</span>
            ) : (
              ""
            )}
            <div className="add-note-buttons">
              <Button variant="flat" onClick={this.closeDialog.bind(this)}>
                CANCEL
              </Button>
              <Button
                variant="flat"
                onClick={this.submitDialog.bind(this)}
                type="submit"
                value="submit"
              >
                PASS
              </Button>
            </div>
          </Form>
        </Modal>
      </div>
    );
  }
}
