import React from "react";
import Row from "muicss/lib/react/row";
import Col from "muicss/lib/react/col";

export default class DocumentLink extends React.Component {
  constructor(props) {
    super(props);
    this.handleClick = this.handleClick.bind(this);
  }

  async handleClick() {
    await this.props.getGoogleDoc(this.props.id);
  }

  render() {
    return (
      <div>
        <Row className="documents">
          <Col md="9" className="document-name">
            <i className="material-icons">description</i>
            <p onClick={this.handleClick}>{this.props.title}</p>
          </Col>
          <Col md="3" className="document-download">
            <div href="#" onClick={this.handleClick}>
              <div className="download-tooltip">
                <i className="material-icons doc-download">file_download</i>
                <span className="download-tooltiptext">Download</span>
              </div>
            </div>
          </Col>
        </Row>
      </div>
    );
  }
}
