import React from "react";
import Deal from "./Deal";
import { Navigation } from "../../lib";
import Dealinfo from "../common/Dealinfo";
import Modal from "react-modal";
import encodeQueryData from "../common/encodeQueryData";

export default class AwaitingTermSheetPage extends React.Component {
  getAwaitingSheetInfo(item) {
    let underwriterBrokers = item.brokers
      .filter((b) => b.is_underwriter === 1)
      .map(function (b) {
        return b.first_name + " " + b.last_name;
      })
      .join(", ");
    let mainBrokers = item.brokers
      .filter((b) => b.broker_of_record === 1 || b.face_of_deal === 1)
      .map(function (b) {
        return b.first_name + " " + b.last_name;
      })
      .join(", ");

    let dealInfo = [];
    dealInfo.push(
      <Dealinfo key="1" title="Quote Date" value={item.req_date} />
    );
    dealInfo.push(
      <Dealinfo key="3" title="Main Brokers" value={mainBrokers} />
    );
    dealInfo.push(
      <Dealinfo key="4" title="Sr UW/processor" value={underwriterBrokers} />
    );
    dealInfo.push(
      <Dealinfo key="5" title="Client Name" value={item.contact_name} />
    );
    return dealInfo;
  }

  dealClick(dealId, violations, additional_properties, page, e) {
    e.stopPropagation();
    if (violations != null || additional_properties > 0) {
      this.props.setPageAndScrollPos("/missingQuotes/quote", window.scrollY);
      Navigation.go(
        `/MQdeal-details/${dealId}/${page}?${encodeQueryData({
          violations,
          additional_properties,
          previous_page: encodeURIComponent("/missingQuotes/termSheet"),
        })}`
      );
    }
  }

  compsClick(inNYC, address, zip, property_type_id) {
    sessionStorage.setItem("comps_inNYC", inNYC);
    this.props.setPageAndScrollPos("/missingQuotes/termSheet", window.scrollY);
    this.props.submitCompsSearch(`${address} ${zip}`, property_type_id);
  }

  render() {
    const awaitingSheet =
      !this.props.noQuotes &&
      this.props.awaitingSheet &&
      this.props.awaitingSheet.map((item) => (
        <Deal
          key={item.quote_id}
          dealId={item.deal_id}
          quote_id={item.quote_id}
          property_type_id={item.property_type_id}
          navChange={this.props.navChange}
          clickFunction={this.dealClick.bind(this)}
          dealTitle={item.deal_name}
          name={item.deal_name}
          address={item.property_address}
          zip={item.city_state_zip}
          otherProperty={item.count_other_properties}
          info={this.getAwaitingSheetInfo(item)}
          submission={item.write_up}
          complaints={item.total_complaints}
          violations={item.total_violations}
          comps={item.TotalComps}
          inNYC={item.in_nyc}
          compsClick={this.compsClick.bind(this)}
          quoteText="Edit Quote"
          documents={item.documents}
          getGoogleDoc={this.props.getGoogleDoc}
          setPageAndScrollPos={this.props.setPageAndScrollPos}
        />
      ));

    return (
      <div>
        {awaitingSheet}
        <Modal
          className="loading-modal"
          isOpen={this.props.loading}
          ariaHideApp={false}
          contentLabel="Modal"
          shouldCloseOnOverlayClick={false}
        >
          <div className={this.props.loading ? "centered-spinner" : "hidden"}>
            <img
              src="/images/icons/spin.svg"
              className="loading-spinner"
              alt="loading spinner"
            />
          </div>
        </Modal>
        <div className={this.props.noQuotes ? "" : "hidden"}>
          <div>
            <div className="grey-circle">
              <i className="material-icons">warning</i>
            </div>
            <h2 className="no-results">No Missing Quotes</h2>
          </div>
        </div>
      </div>
    );
  }
}
