import React from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import * as missingQuotesActions from "../../actions/missingQuotesActions";
import * as navigationTitleActions from "../../actions/navigationTitleActions";
import * as compsActions from "../../actions/compsActions";
import AwaitingTermSheet from "./AwaitingTermSheetPage";
import Expire from "../common/Expire";
import Button from "muicss/lib/react/button";

class MissingQuotesContainer extends React.Component {
  constructor(props) {
    super(props);
    this.navChange = this.navChange.bind(this);
    // this.quoteDialog = this.quoteDialog.bind(this);
  }

  componentDidMount() {
    this.props.actions.loadAwaitingTermSheets();
    this.props.navActions.updateNavTitle("Waiting Term Sheets");
    if (
      this.props.comps.previousScrollPos &&
      this.props.comps.previousScrollPos.length
    ) {
      window.scrollTo(
        0,
        this.props.comps.previousScrollPos[
          this.props.comps.previousScrollPos.length - 1
        ]
      );
      this.props.compsActions.resetScrollPosition();
    }
  }

  navChange(title, violations) {
    if (violations >= 1) {
      this.props.navActions.updateNavTitle(title);
    }
  }
  //
  // quoteDialog(quote_id){
  //   this.props.compsActions.setPageAndScrollPos("/missingQuotes/termSheet", document.body.scrollTop);
  //   var path = encodeURIComponent(window.location.pathname);
  //   Navigation.go(`/quote-form/1/${quote_id}/${path}`);
  // }

  render() {
    let successMessage = null;
    if (this.props.missingQuotes.toast && this.props.missingQuotes.toast.show) {
      successMessage = (
        <Expire closeToast={this.props.actions.closeToast} delay={6000}>
          <div className="success-message">
            <p>{this.props.missingQuotes.toast.message}</p>
            <Button variant="flat" onClick={this.props.actions.closeToast}>
              {this.props.missingQuotes.toast.success ? "Done" : "Dismiss"}
            </Button>
          </div>
        </Expire>
      );
    }
    return (
      <div>
        {successMessage}
        <AwaitingTermSheet
          awaitingSheetCount={this.props.missingQuotes.awaitingSheetCount}
          awaitingSheet={this.props.missingQuotes.awaitingSheet}
          loading={this.props.missingQuotes.loading}
          noQuotes={this.props.missingQuotes.noQuotes}
          navChange={this.navChange}
          // quoteDialog={this.quoteDialog}
          setPageAndScrollPos={this.props.compsActions.setPageAndScrollPos}
          submitCompsSearch={this.props.compsActions.submitCompsSearch}
          getPropertyDocuments={this.props.actions.getPropertyDocuments}
          getGoogleDoc={this.props.actions.getGoogleDoc}
        />
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    title: state.navTitle,
    missingQuotes: state.missingQuotes,
    comps: state.comps,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(missingQuotesActions, dispatch),
    navActions: bindActionCreators(navigationTitleActions, dispatch),
    compsActions: bindActionCreators(compsActions, dispatch),
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(MissingQuotesContainer);
